<template>
  <div class="distribution">
    <div class="map" id="map"></div>
    <div class="opt">
      <div class="opt-item">
        <input type="date" v-model="date" @change="fetchPOI" />
      </div>
      <select v-model="layer">
        <option value="vector" label=""></option>
        <option value="satellite" label="ESRI"></option>
        <option value="world_street" label="ESRI"></option>
      </select>
    </div>
    <Loading v-if="loading" left="95%" top="5%" size="1vw" />
  </div>
</template>

<script>
import { Map, View } from 'ol';
import { Vector as vLayer, Tile } from 'ol/layer';
import { Vector as VSource, XYZ, TileArcGISRest as ArcgisSource } from 'ol/source';
import { fromLonLat } from 'ol/proj';
import Loading from '@/components/Loading.vue';
import { blogStasis } from '@/api';
import { Point } from 'ol/geom';
import Feature from 'ol/Feature';
import {fmtNowDate} from '@/utils/tool';


/**
 * @type {Map}
 */
let map;
let poiLyr;

export default {
  name: 'Distribution',
  components: { Loading },
  data() {
    return {
      loading: false,
      layer: 'vector',
      date: null,
    };
  },
  computed: {},
  watch: {
    layer(val) {
      this.changeLayer(val);
    },
  },
  beforeMount() {
    this.date = fmtNowDate()
  },
  mounted() {
    this.initMap();
    this.changeLayer(this.layer);
    this.fetchPOI();
  },
  beforeUnmount() {
    if (typeof map == 'undefined') return;
    map.un('rendercomplete', this.loadCompleteEvtHandler.bind(this));
    map.un('loadstart', this.loadStartEvtHandler.bind(this));
  },
  methods: {
    initMap() {
      map = new Map({
        target: 'map',
        layers: [],
        view: new View({
          zoom: 4,
          center: fromLonLat([100, 39]),
          minZoom: 3,
        }),
      });
      map.on('rendercomplete', this.loadCompleteEvtHandler.bind(this));
      map.on('loadstart', this.loadStartEvtHandler.bind(this));
      window.map = map;
    },
    loadCompleteEvtHandler(evt) {
      this.loading = false;
    },
    loadStartEvtHandler(evt) {
      this.loading = true;
    },
    changeLayer(name) {
      for (let lyr of map.getAllLayers()) {
        console.log(lyr.id);
        if (lyr.name != 'poi') {
          map.removeLayer(lyr);
        }
      }
      let lyr;
      if (name == 'vector') {
        lyr = new Tile({
          name: name,
          zIndex: 0,
          source: new XYZ({
            url: 'https://webrd02.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}',
          }),
        });
      } else if (name == 'satellite') {
        lyr = new Tile({
          name: name,
          zIndex: 0,
          source: new ArcgisSource({
            url: 'https://server.arcgisonline.com/arcgis/rest/services/World_Imagery/MapServer',
          }),
        });
      } else if (name == 'world_street') {
        lyr = new Tile({
          name: name,
          zIndex: 0,
          source: new ArcgisSource({
            url: 'https://server.arcgisonline.com/arcgis/rest/services/World_Street_Map/MapServer',
          }),
        });
      }
      if (typeof lyr != 'undefined') {
        map.addLayer(lyr);
      }
    },
    fetchPOI() {
      if (typeof poiLyr != 'undefined') {
        map.removeLayer(poiLyr);
        poiLyr = undefined;
      }
      const params = {'date': this.date};
      blogStasis({ type: 'distribution' , params}).then(res => {
        /**
         * @type {Array}
         */
        let pois = res.data.data;
        let feats = pois.map(item => {
          return new Feature({
            geometry: new Point(fromLonLat([item.lon, item.lat])),
          });
        });
        poiLyr = new vLayer({
          name: 'poi',
          source: new VSource({
            features: feats,
          }),
          zIndex: 99,
        });
        map.addLayer(poiLyr);
      });
    },
  },
};
</script>

<style scoped lang="scss">
.distribution {
  position: relative;
  width: 100%;
  height: 100%;
  #map {
    width: 100%;
    height: 100%;
  }
  .opt {
    display: flex;
    flex-direction: column;
    gap: 5px;
    position: absolute;
    left: 1%;
    bottom: 1%;
    padding: 5px;
    background-color: $--baseColor;
    border-radius: 5px;
    ul {
      list-style: none;
    }
    input {
      flex: 1;
    }
  }
}
</style>
